<template>
  <div class="login">
      <div class="wrap">
          <div class="logo">
              <p>汽车座椅舒适性乘员感知评价系统</p>
          </div>
          <div class="content">

                <div v-if="STATUS == 'login' || STATUS == 'singup'">
                    
                    <div class="item" v-if="STATUS == 'singup'">
                        <van-icon name="logistics" />
                        <input type="text" v-model="company_name" placeholder="请输入厂商名称" name="" id="">
                    </div>

                    <div class="item" v-if="STATUS == 'singup'">
                        <div class="l">
                            <van-icon name="phone-o" />
                            <input type="text" v-model="mobile" placeholder="请输入手机号" name="" id="">
                        </div>
                        <div class="r">
                            <p @click="sendcode">{{sendCodeStatus}}</p>
                        </div>
                    </div>
                    <div class="item" v-if="STATUS == 'singup'">
                        <van-icon :name="eyeStatusName" @click="changeType" />
                        <input :type="eyeStatus" v-model="password" placeholder="请输入密码" name="" id="">
                    </div>

                    <div class="item" v-if="STATUS == 'singup'">
                        <van-icon name="comment-o" />
                        <input type="text" v-model="code" placeholder="请输入验证码" name="" id="">
                    </div>

                    <div class="item" v-if="STATUS == 'login'">
                        <div class="l">
                            <van-icon name="phone-o" />
                            <input type="text" v-model="mobile" placeholder="请输入手机号" name="" id="">
                        </div>
                        <!-- <div class="r">
                            <p @click="sendcode">{{sendCodeStatus}}</p>
                        </div> -->
                    </div>
                    <div class="item" v-if="STATUS == 'login'">
                        <van-icon :name="eyeStatusName" @click="changeType" />
                        <input :type="eyeStatus" v-model="password" placeholder="请输入密码" name="" id="">
                    </div>




                </div>

                <div class="item" v-if="STATUS == 'code'">
                    <van-icon name="qr" />
                    <input type="text" placeholder="请输入授权码" name="" id="">
                </div>
          </div>
          
          <div class="btn-wrap">
                <div class="button" v-if="STATUS == 'login'" @click="toLogin">登录</div>
                <div class="button" v-if="STATUS == 'singup'" @click="singup">注册</div>
                <div class="button" v-if="STATUS == 'code'">匿名登录</div>
                
                <div class="tab">
                    <span v-if="STATUS != 'login'" @click="STATUS = 'login'">登录</span>
                    <span v-if="STATUS != 'singup'" @click="STATUS = 'singup'">注册</span>
                    <span v-if="STATUS != 'code'" @click="STATUS = 'code'">匿名登录</span>
                </div>
          </div>

      </div>
  </div>
</template>
 
<script>
// import mInput from '../components/mInput.vue'
import api from '../api/index'
import qs from 'qs';
export default {
    name: 'login',
    components: {
        // mInput
    },
    data() {
        return {
            STATUS: 'login', // login singup code
            val: '',
            timer: '',
            sendCodeStatus: '发送验证码',
            mobile: '',
            code: '',
            password: '',
            company_name: '',
            eyeStatus: 'password',
            eyeStatusName: 'closed-eye',
        }
    },
    methods: {
        toLogin() {
            console.log("登录")
            
            api.singIn(qs.stringify({
                account: this.mobile,
                password: this.password
            })).then(res => {
                console.log(res)
                alert(res.data.msg)
                if(res.data.code == 1) {
                    this.userInfo = res.data.data.userinfo;
                    localStorage.setItem('userInfo__xes', JSON.stringify(this.userInfo));
                    this.$router.push({name: 'home'})
                }
            })
            
        },
        sendcode() {
            if(this.sendCodeStatus == '发送验证码') {
                let count = 60;
                this.sendCodeStatus = count;
                this.timer = setInterval(()=> {
                    count--
                    this.sendCodeStatus = count;
                    if(count == 0) {
                        this.sendCodeStatus = '发送验证码';
                        clearInterval(this.timer);
                    }
                }, 1000)
                api.sendCode({
                    mobile: this.mobile,
                    scene: 'register '
                }).then(res => {
                    console.log(res)
                    this.code = res.data.data.code;
                })
            }
        },
        changeType() {
            // console.log(this.eyeStatus)
            switch(this.eyeStatus) {
                case 'password':
                    this.eyeStatus = 'text';
                    this.eyeStatusName = 'eye-o';
                    break;
                case 'text':
                    this.eyeStatus = 'password';
                    this.eyeStatusName = 'closed-eye';
                    break;
            }
        },
        singup() {
            console.log("注册！");
            api.singUp({
                company_name: this.company_name,
                mobile: this.mobile,
                password: this.password,
                code: this.code
            }).then(res => {
                console.log(res)
                alert(res.data.msg)

            })
        }
    }
}
</script>
 
<style scoped lang ="stylus">
 .login
    width 100%
    height 100vh
    background-image url('~@/assets/img/bg_2.png')
    background-size 100% 100%
    background-repeat no-repeat
    .wrap
        display flex
        flex-direction column 
        justify-content center
        align-items center
        padding-top 80px
        .logo
            p
                font-size 22px
                color #C4D0E4
                font-weight bold
        .content
            width 80%
            padding 22px
            margin-top 40px
            border 1px solid #FFF
            border-radius 8px
            background rgba(255,255,255,.3)
            .item 
                display flex
                align-items center
                background #DFEBFF
                margin-bottom 12px
                height 38px
                font-size 22px
                border-radius 4px
                overflow hidden
                .van-icon
                    width 38px
                    background #DFEBFF
                    text-align center
                input 
                    flex 1
                    height 38px
                    border none
                    font-size 14px
                    padding 0 6px
                    background #C0DDFF
                .l 
                    display flex
                    align-items center
                    flex 1
                    font-size 22px
                .r 
                    background #2562AC
                    height 38px
                    p 
                        font-size 12px
                        padding 0 6px
                        line-height 38px
                        width 62px
                        text-align center
                        color #FFF
        .btn-wrap
            padding-top 80px
            width 100%
            display flex
            justify-content center
            flex-wrap wrap
            .button
                background #EEE
                width 70%
                text-align center
                padding 12px 0
                background #2562AC
                /* background linear-gradient(45deg, #03C9FF, #438FFC) */
                color #FFF
                border-radius 4px
        .tab
            width 70%
            display flex
            justify-content space-between
            padding-top 8px
            span 
                margin-right 12px
                font-size 14px
                color #70829D




.van-hairline--top-bottom::after,
.van-hairline-unset--top-bottom::after
    border none

</style>